<template>
  <van-pull-refresh
    v-model="refreshing"
    @refresh="onRefresh"
  >
    <van-list
      v-model="loading"
      @load="onLoad"
      :finished="finished"
      finished-text="我也是有底线的"
    >
      <main>
        <nav>
          <div @click="handleCityClick">
            {{city}}
            <i class="city-entry-arrow"></i>
          </div>
          <div>
            <ul>
              <router-link tag="li" active-class="active" to="/movie/intheaters">热映</router-link>
              <router-link tag="li" active-class="active" to="/movie/comingsoon">待映</router-link>
            </ul>
          </div>
          <div></div>
        </nav>
        <div class="movie-content">
          <transition
            :enter-active-class="`animate__animated animate__${enter}`"
            :leave-active-class="`animate__animated animate__${leave}`"
          >
            <router-view :hot-list="hotlist"></router-view>
          </transition>
        </div>
      </main>
    </van-list>
  </van-pull-refresh>
</template>

<script>
import Vue from 'vue'
import { List, PullRefresh } from 'vant'

Vue.use(List)
Vue.use(PullRefresh)

import http from '@u/http'
import { mapState } from 'vuex'
export default {
  data() {
    return {
      hotlist: [],
      allHotList: [],
      loading: false,
      finished: false,
      refreshing: false,
      enter: '',
      leave: ''
    }
  },

  created() {
    // 初始化翻页数据
    this.limit = 12
    this.page = 0
  },

  mounted() {
    // this.loadData()
  },

  computed: {
    ...mapState(['city'])
  },

  watch: {
    $route(to, from) {
      if (to.meta.order > from.meta.order) {
        this.enter = 'slideInRight'
        this.leave = 'slideOutLeft'
      } else {
        this.enter = 'slideInLeft'
        this.leave = 'slideOutRight'
      }
    }
  },

  methods: {
    async loadData() {
      // 表示用户下拉，需要将Page的页码重置为0, 同时清空list，不影响上拉加载
      if (this.refreshing) {
        this.hotlist = []
        this.page = 0
      }

      let result = await http.get('/api/mmdb/movie/v2/list/hot.json', {
        limit: this.limit, // pageSize: 每页多少条
        offset: this.limit * this.page, // 分页的起始点
        ct: this.$store.state.city
      })
      
      this.hotlist = [
        ...this.hotlist,
        ...result.data.hot
      ]

      this.loading = false
      this.refreshing = false
      this.page++

      if (this.page >= Math.ceil(result.data.total / this.limit)) {
        this.finished = true
      }

      await this.$nextTick()
      document.querySelector('.main').scrollTo(0, this.$store.state.scrollTop)

    },

    onLoad() {
      this.loadData()
    },

    onRefresh() {
      this.loadData()
    },

    handleCityClick() {
      this.$router.push('/city')
    },
  }
}
</script>

